import Card from "../view/Card";
import CardTitle from "../view/CardTitle";
import InputWithIcon from "../view/InputWithIcon";
import ForecastUnselectedFieldsStub from "../view/ForecastUnselectedFieldsStub";

function WeekForecast() {
    return (
        <>
            <Card className="card-main">
                <CardTitle text="7 Days Forecast"/>
                <InputWithIcon/>
                <ForecastUnselectedFieldsStub/>
            </Card>
        </>
    );
}

export default WeekForecast;