
function FlexRow({ children, className=""}) {
    const flexRowClassName =  `flex-row ${className}`;

    const childrenFlex = children.map(el => <div className="flex-row__child">{ el }</div>)

    return (
        <>
            <div className={flexRowClassName}>
                { childrenFlex }
            </div>
        </>
    );
}

export default FlexRow;