import Card from "../view/Card";
import CardTitle from "../view/CardTitle";
import InputWithIcon from "../view/InputWithIcon";
import FlexRow from "../view/FlexRow";

function DateForecast() {
    return (
        <>
            <Card className="card-main">
                <CardTitle text="Forecast for a Date in the Past"/>
                <FlexRow>
                    <InputWithIcon/>
                    <InputWithIcon/>
                </FlexRow>
                
            </Card>
        </>
    );
}

export default DateForecast;